<template>
  <view class="table">
    <view class="title">
      <view
        :class="{
          column3: tableColumn == 3,
          column4: tableColumn == 4,
          column5: tableColumn == 5,
        }"
        v-for="item in title"
      >
        {{ item }}
      </view>
    </view>
    <view class="content">
      <scroll-view
        class="scroll"
        scroll-y
        @scrolltolower="handleScrolltolower"
      >
        <view class="item" v-for="item in tableData">
          <view
            :class="{
              column3: tableColumn == 3,
              column4: tableColumn == 4,
              column5: tableColumn == 5,
            }"
            v-for="attr in item"
          >
            {{ attr }}
          </view>
        </view>
        <view class="loading">
          <text v-if="loading == 'loadmore'">加载更多</text>
          <view v-if="loading == 'loading'" class="cu-load loading"></view>
          <view v-if="loading == 'nomore'" class="cu-load over"></view>
        </view>
      </scroll-view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'wTable',
  props: {
    title: {
      type: Array,
    },
    tableData: {
      type: Array,
    },
    loading: {
      type: String,
      default: 'loadmore',
    },
  },
  computed: {
    tableColumn() {
      //计算表格的列数
      return this.title.length ? this.title.length : 4
    },
  },
  methods: {
    handleScrolltolower(e) {
      const {
        detail: { direction },
      } = e
      if (direction !== 'bottom') return
      this.$emit('reachBottom')
    },
  },
}
</script>

<style lang="scss" scoped>
.table {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  .column3 {
    width: 33.33333%;
    text-align: center;
  }

  .column4 {
    width: 25%;
    text-align: center;
  }

  .column5 {
    width: 20%;
    text-align: center;
  }
  .title {
    background-color: #e6f5ff;
    height: 92rpx;
    display: flex;
    align-items: center;
    font-size: 30rpx;
    font-family: Source Han Sans CN, Source Han Sans CN-Bold;
    font-weight: 700;
  }

  .content {
    flex: 1 0;
    min-height: 0;
    background-color: #fff;
    font-size: 28rpx;
    color: #666666;
    .scroll {
      height: 100%;
    }

    .item {
      display: flex;
      align-items: center;
      padding: 40rpx 0 30rpx 0;
      border-bottom: 2rpx solid #dfe6ec;
    }
  }
  .loading {
    padding-bottom: 10rpx;
    text-align: center;
    font-size: 24rpx;
    color: #a4a7ac;
  }
}
</style>
